.template__container {
  max-width: 50rem;
  background-color: $color-white;
  border-radius: 12px;
  padding: 2rem;
  @include mboxshadow(1); // depth level 1
  margin-bottom: 2rem;
}

.template__title__container {
  margin: 0px 20px;
  margin-top: -45px;
  margin-bottom: 15px;
  border-radius: 6px;
  background: linear-gradient(45deg, $color-blue, $color-primary);
  box-shadow: 0 12px 20px -10px rgba($color-blue, 1),
    0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba($color-primary, 0.2);
}
.gradient__warm {
  background: linear-gradient(45deg, $color-negative, $color-warning);
  box-shadow: 0 12px 20px -10px rgba($color-negative, 1),
    0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba($color-warning, 0.2);
}

input#listName,
input#title,
input#sender {
  border-bottom: 0.1rem solid rgba($color-grey-dark, 0.2);
  &:focus {
    border-bottom: 0.1rem solid $color-primary;
  }
}
// New SMS  (Quick SMS)
.sms__container {
  padding: 5rem 3rem;
}

select#choosenTemplate {
  width: 100%;
  height: 100%;
  padding: 1rem;
}

// Numbers Tags Imput RESET
.react-tagsinput {
  width: 100%;
  height: 100%;
  border: 0.1rem solid rgba($color-grey-dark, 0.2);
  overflow: auto;
  padding: 0.5rem;
  background-color: $color-grey-light;
}
.react-tagsinput--focused {
  border: 1px solid $color-primary;
}

.react-tagsinput-input {
  background: transparent;
  font-weight: $font-weight-light;
  font-size: $font-size-regular;
  line-height: $line-height-regular;
  color: $color-grey-dark;
  margin-bottom: 6px;
  margin-top: 1px;
  outline: none;
}

.react-tagsinput-tag {
  background-color: $color-primary;
  color: $color-grey-light;
  font-weight: $font-weight-light;
  border: 1px solid $color-primary;
  border-radius: 2px;
  display: inline-block;
  font-size: $font-size-regular;
  margin-bottom: 0.5rem;
  margin-right: 0.5rem;
  padding: 0.5rem;
}

.react-tagsinput-remove {
  cursor: pointer;
  font-weight: bold;
}

.react-tagsinput-tag a::before {
  content: " ";
}

.upload-btn-wrapper {
  position: relative;
  overflow: hidden;
  display: inline-block;
  cursor: pointer;
}

.btn {
  border: 1px solid $color-blue-grey;
  color: $color-blue-grey;
  padding: 8px 20px;
  font-size: $font-size-small;
  font-weight: bold;
}

.upload-btn-wrapper input[type="file"] {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
